import React, { Component } from 'react';

class Resume extends Component {

  render() {

    return (
      <div id='resumeDiv'>
        <iframe className='resume' src="https://drive.google.com/file/d/1_z7ZpOPsDYYBVbg3zNwIASaXliISZsb1/preview" frameBorder="0" hspace="0" vspace="0" marginheight="0" marginwidth="0" id='resumeFrame' scrolling="no"></iframe>
      </div>
    );
  }
}

export default Resume;



